@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital@1&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  border: 0;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: #fff !important;
}

/* NAV */
nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 25px;
}

nav > div {
  display: flex;
  flex-direction: column;
}

.nav-btn {
  display: flex;
  padding: 9px 20px;
  border-radius: 4px;
  background-color: #0077c5;
  color: white;
  border: 1px solid black;
  align-items: baseline;
  margin-left: 10px;
  cursor: pointer;
}

.nav-btn:checked {
  border: 1px solid black;
}

.nav-btn i {
  margin-right: 10px;
}

nav div span {
  font-size: 12px;
  margin: 0 0 3px 3px;
  color: #717171;
}

nav div a {
  color: #1689d0;
  font-size: 12px;
}

/* MAIN */
.sign-container {
  max-width: 380px;
  margin: 25px auto;
}
.loading_user_gif {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}

.links-container {
  display: flex;
  justify-content: center;
  color: #f3558e;
}

.links {
  display: flex;
  justify-content: center;
}
/* FORM */
.form-container {
  background-color: #cacaca;
  margin-top: 15px;
  box-shadow: 1px 1px 1px -1px #000000;
}

.login {
  padding: 30px 30px 0 30px;
  display: flex;
  flex-direction: column;
  color: #343a40;
  border: 10px solid #373333e3;
  border-bottom: 0;
  background-color: #cacaca;
}

.login-inverse {
  padding: 30px 30px 0 30px;
  display: flex;
  flex-direction: column;
  color: #cacaca;
  background-color: #373333e3;
  border: 10px solid #cacaca;
  border-bottom: 0;
}

.title {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-bottom: 15px;
  color: #e1e8eb;
  font-family: 'Orbitron', sans-serif;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
}

.paragraph {
  text-align: center;
}

.paragraph p {
  font-size: 14px;
}

.paragraph a {
  cursor: pointer;
  font-size: 12px;
  color: #1689d0;
}

.form {
  margin: 25px 0 35px 0;
  font-size: 15px;
}

.form fieldset {
  margin-bottom: 35px;
}

.form fieldset label span {
  color: #b22424;
}

.form input {
  padding: 9px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 3px;
  margin-top: 10px;
}

.form button {
  padding: 13px;
  width: 100%;
  border: 1px solid rgb(224 213 156);
  background-color: #4c4949;
  color: #e2f3f5;
  box-shadow: 0px 1px 3px 1px rgb(0 0 0 / 75%);
  font-size: 16px;
  cursor: pointer;
  text-transform: uppercase;
  font-family: 'Orbitron', sans-serif;
}
.btn-inverse {
  background-color: #cacaca !important;
  color: #373333e3 !important;
  text-transform: uppercase;
  font-family: 'Orbitron', sans-serif;
}

.form button:focus {
  border: 1px solid black;
}

.form button i {
  margin-right: 10px;
}

.footer-form {
  font-size: 10px;
  color: #ffffff;
  text-align: center;
  background-color: #464343;
  border: 10px solid #464343;
  padding: 25px 0;
  border-top: 0;
}
.footer-form-inverse {
  background-color: #cacaca !important;
  border: 10px solid #cacaca !important;
  font-size: 10px;
  color: #ffffff;
  text-align: center;
  padding: 25px 0;
  border-top: 0;
}
.top {
  display: flex;
  flex-direction: column;
}
.redirect {
  font-size: 12px;
  color: #cacaca;
  text-align: center;
}
.redirect a:hover {
  text-decoration: underline;
}

.errors {
  font-size: 13px;
  color: #b22424;
}

.nav-flex {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
}
.main-username {
  margin: 0px 5px;
  font-size: 14px;
}

.logo-container {
  font-family: 'Crimson Text', serif;
  position: relative;
}
.logo {
  overflow: hidden;
  margin-top: 10px;
  margin-right: 30px;
  box-shadow: 0 4px 3px 3px rgb(0 0 0 / 60%);
  background-color: rgb(255 149 0);
  transform: translatey(5px);
  animation: float-1 4s ease-in-out infinite;
  width: 75px;
}
.logo-container h2 {
  color: rgb(255 149 0);
  position: absolute;
  top: -25px;
  text-transform: uppercase;
  left: 80%;
  font-size: xx-large;
}
.logo-container h3 {
  position: absolute;
  top: -50px;
  text-transform: uppercase;
  left: 85%;
  font-size: 24px;
  color: #42ef23;
}
@keyframes float-1 {
  0% {
    box-shadow: 0 -10px 19px 1px rgb(255 117 33 / 70%);
    transform: translatey(5px);
  }
  50% {
    box-shadow: 0 10px 19px 1px rgb(255 117 33 / 70%);
    transform: translatey(-10px);
  }
  100% {
    box-shadow: 0 -10px 19px 1px rgb(255 117 33 / 70%);
    transform: translatey(5px);
  }
}
.sign-out {
  background: none !important;
}
.nav-flex-right svg {
  width: 60px;
  margin-top: 10px;
}
.nav-flex-right {
  width: 350px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.flex-right-header {
  background-color: #575859;
  transition: all 0.7s ease-in-out;
  height: 30px;
  border-radius: 15px;
  width: 55%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
.flex-right-header:hover {
  background-color: #ff9500;
  width: 70%;
}
.user-icon {
  border-radius: 50%;
  position: absolute;
  left: 3px;
  top: 3px;
  width: 25px;
}
.shop-icon {
  transition: 0.3s ease-in-out all;
}
.out-icon {
  transition: 0.6s ease-in-out all;
}
.out-icon:hover {
  color: darkred !important;
}
.shop-icon:hover {
  color: white !important;
}

@media only screen and (max-width: 680px) {
  .main-username {
    display: none;
  }
  .flex-right-header {
    width: 11%;
    justify-content: center;
  }
  .logo-container h3,
  .logo-container h2 {
    font-size: 20px;
  }
  .user-icon {
    border-radius: 50%;
    position: relative;
    left: 0;
    top: 2px;
    width: 25px;
  }
  .logo-div {
    display: none;
  }
  .logo {
    animation: none;
    width: 60px;
    margin-top: 0;
  }
  .nav-flex {
    position: fixed;
    top: 0;
    z-index: 99;
    align-items: center;
    justify-content: space-between;
    background-color: black;
    width: 100%;
  }
  .nav-flex-right {
    width: 200px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
  }
  .user-icon {
    width: 35px;
  }
  .flex-right-header {
    width: 85%;
    height: 45px;
  }
  .nav-flex-right svg {
    margin-top: 0;
    display: flex;
  }
}

button {
  cursor: pointer;
}
.product-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 40px;
  margin: 40px;
}
.product-container {
  border: 10px solid #535151;
}
.product-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 10px solid #fff;
  border-top: 40px solid #f29718;
  border-bottom: 25px solid rgb(255 149 0);
  height: 545px;
}
.gif-loading {
  display: flex;
  margin: auto;
}
.product-container {
  position: relative;
  background-color: #ff9500;
}
.product-img-div {
  position: absolute;
  z-index: 2;
  height: 205px;
  /* box-shadow: rgb(0 0 0 / 25%) 0px 54px 55px, rgb(0 0 0 / 12%) 0px -12px 30px,
    rgb(0 0 0 / 12%) 0px 4px 6px, rgb(0 0 0 / 17%) 0px 12px 13px,
    rgb(0 0 0 / 9%) 0px -3px 5px; */
  width: 100%;
  top: 5%;
}

.product-img-div p {
  position: absolute;
  left: 5%;
  z-index: 2;
  top: 5%;
  padding: 5px;
  background-color: rgb(255 149 0);
  color: white;
  font-weight: 900;
}

.exit-popup {
  position: absolute;
  padding: 5px 10px;
  height: 25px;
  top: 5%;
  right: 5%;
  background-color: #373333;
  color: white;
  margin-left: auto;
}
.img-container {
  z-index: 1;
  position: absolute;
  width: 100%;
}
.product-img-div img {
  width: 100%;
  height: 250px;
}

.product-desc-div {
  background-color: rgb(255 149 0);
  height: 100%;
  width: 100%;
  text-align: start;
  color: white;
  padding: 25px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 14px;
  box-shadow: rgb(0 0 0 / 6%) 0px 8px 8px 3px inset;
}
.product-desc-div a {
  color: #476899 !important;
  /* display: block; */
}

.product-desc-div span {
  margin: 0 0 10px 0;
}
.product-desc-div h3 {
  /* font-family: 'Raleway', sans-serif; */
  font-family: 'Source Sans Pro', sans-serif;
  text-transform: capitalize;
}

.cart-off {
  color: #ff9500;
  background: white;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border: 5px solid #ffac00e0;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.pop-up {
  position: fixed;
  width: 300px;
  top: 0;
  left: 40%;
  z-index: 101;
  border: 10px solid white;
  display: flex;
  flex-direction: column;
  margin: 10px 15px;
  align-items: center;
  border: 10px solid #fff;
  border-top: 40px solid #f29718;
  border-bottom: 30px solid rgb(255 149 0);
}
.product-img-div-pop-up {
  position: relative;
  height: 250px;
  width: 100%;
}

.product-img-div-pop-up p {
  position: absolute;
  left: 5%;
  top: 5%;
  padding: 5px;
  background-color: rgb(255 149 0);
  color: white;
  font-weight: 900;
}

.product-img-div-pop-up img {
  width: 100%;
  height: 250px;
}

.product-desc-div-pop-up {
  background-color: rgb(255 149 0);
  height: 100%;
  width: 100%;
  text-align: start;
  color: white;
  padding: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 14px;
  box-shadow: rgb(0 0 0 / 6%) 0px 8px 8px 3px inset;
}
.layer {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(0 0 0 / 70%);
  z-index: 100;
}
#layer-loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(0 0 0 / 40%);
  z-index: 4;
  margin: auto;
}
.mobile-btn {
  display: none;
}
@media only screen and (max-width: 1200px) {
  .product-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media only screen and (max-width: 930px) {
  .pop-up {
    left: 30%;
  }
  .product-desc-div {
    height: 65%;
    margin: auto 0;
  }
  .product-grid {
    grid-template-columns: 1fr 1fr;
    margin: 40px 50px;
    grid-gap: 20px;
  }
  .product-box {
    margin: 0;
  }
  .product-img-div {
    width: 100%;
    left: 0;
  }
  .product-desc-div {
    padding-bottom: 0;
  }
  .product-grid * {
    font-size: 17px !important;
  }
  .product-desc-div span {
    font-size: 15px !important;
  }
  .product-desc-div strong {
    font-size: 15px !important;
  }
}
@media only screen and (max-width: 680px) {
  .gif-loading {
    display: flex;
    margin: auto;
    width: 100vw;
    height: 100vh;
  }
  .pop-up {
    left: 16%;
  }
  .not-mobile-btn,
  .empty-div {
    display: none;
  }
  .mobile-btn {
    display: block;
  }
  .product-grid {
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 100px;
  }
  .product-box {
    margin: 0;
    border: 0;
    box-shadow: none;
    display: flex;
    height: 95%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .product-img-div {
    position: relative;
    top: 0;
  }
  .product-img-div img {
    height: 100%;
  }
  .product-container {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .img-container {
    height: 100%;
  }
  .product-desc-div {
    padding-bottom: 0;
    justify-content: flex-start;
    box-shadow: none;
  }
  .product-grid * {
    font-size: 16px !important;
  }
  .product-desc-div span {
    font-size: 14px !important;
  }
  .product-desc-div strong {
    font-size: 14px !important;
  }
}

/* @import url('https://fonts.googleapis.com/css2?family=Encode+Sans+SC&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap'); */
body {
  background-color: #383838;
}
.cat-main {
  width: 450px;
}
.heart-containers {
  position: relative;
}
.heart-div {
  background: #36ad47;
  transform: rotate(62deg);
  width: 60px;
  padding: 10px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  position: absolute;
  right: 5%;
  top: 30%;
  box-shadow: 0 0px 19px 1px rgb(79 191 97);
  cursor: pointer;
  animation: float-2 1.5s ease-in-out infinite;
}
.green {
  color: #36ad47;
  text-transform: uppercase;
}
.yellow {
  color: #ffe101;
  text-transform: uppercase;
}
.heart-div::after {
  content: ' ';
  width: 0;
  height: 0;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid #36ad47;
  position: absolute;
  transform: rotate(180deg);
  bottom: -15%;
  left: 15%;
}
.heart-div-2 {
  background: #ffe101;
  transform: rotate(316deg);
  width: 60px;
  padding: 10px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  position: absolute;
  left: 17%;
  top: 19%;
  right: 0%;
  box-shadow: 0 0px 19px 1px rgb(254 225 0);
  cursor: pointer;
  animation: float-3 1.5s ease-in-out infinite;
}
.heart-div-2::after {
  content: ' ';
  width: 0;
  height: 0;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  border-bottom: 10px solid #ffe101;
  position: absolute;
  transform: rotate(180deg);
  bottom: -15%;
  left: 15%;
}
@keyframes float-2 {
  0% {
    margin-right: 0;
  }
  50% {
    margin-right: 5px;
  }
  100% {
    margin-right: 0px;
  }
}
@keyframes float-3 {
  0% {
    margin-left: -5px;
  }
  50% {
    margin-left: 0px;
  }
  100% {
    margin-left: -5px;
  }
}
.bracket-1 {
  /* position: absolute; */
  right: 35%;
  color: #ff9500;
  font-size: 25em;
  z-index: 1;
  display: inline;
}
.bracket-2 {
  /* position: absolute; */
  display: inline;
  left: 2px;
  color: #ff9500;
  font-size: 25em;
  z-index: 1;
}
.content {
  z-index: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  width: 85%;
  position: absolute;
  top: 17%;
  height: 316px;
  padding: 30px 100px 30px 50px;
  left: 8%;
  box-sizing: border-box;
  color: #ff9500;
  box-shadow: rgb(50 50 93 / 25%) 0px 30px 60px -12px inset,
    rgb(0 0 0 / 30%) 0px 18px 36px -18px inset,
    rgb(0 0 0 / 30%) -3px -10px 36px -18px inset,
    rgb(0 0 0 / 30%) -20px 0px 36px 10px inset;
}
.content-headline {
  font-family: 'Crimson Text', serif;
}
.shop-btn {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 115px;
  font-weight: bold;
  padding: 12px;
  background: #ff9500;
  color: white;
  box-shadow: 0 0 11px 1px rgb(255 149 0);
  transition: all 0.5s ease-in-out;
}

.shop-btn:hover {
  cursor: pointer;
  border-radius: 25px;
}

.home-container {
  position: relative;
  display: grid;
  grid-template-columns: 3fr 1fr;
}
.section-1,
.section-2 {
  position: relative;
  display: flex;
  align-items: center;
}
.section-1 {
  justify-content: space-between;
}
.arrow {
  bottom: 24%;
  position: absolute;
  width: 100px;
  transform: rotate(90deg) scaleX(-1);
  left: 175px;
}
#like-target {
  display: none;
}
#cat-target {
  display: none;
}
.animation {
  position: absolute;
  animation: fade-away 0.9s ease-in;
  display: block !important;
}
@keyframes fade-away {
  0% {
    transform: scale(2.5);
    bottom: 50%;
  }
  50% {
    transform: scale(1.5);
    bottom: 150%;
    opacity: 0.5;
  }
  100% {
    transform: scale(0);
    bottom: 200%;
    opacity: 0;
    display: none;
  }
}
@media only screen and (max-width: 1028px) {
  .equal {
    display: block;
  }
  .arrow {
    left: 39%;
    position: absolute;
  }
  .section-2 {
    justify-content: center;
  }
  .home-img {
    position: relative;
    width: 500px;
  }
  .home-container {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
  }
  .section-1 {
    flex-direction: column;
    width: 400px;
    margin: 0 auto;
    order: 2;
  }
  .bracket-1 {
    transform: rotate(90deg);
  }
  .bracket-2 {
    transform: rotate(90deg);
  }
  .content {
    width: 391px;
    margin: 0 auto;
    position: absolute;
    top: 27%;
    height: 425px;
    left: 0px;
    padding: 45px 15px 45px 50px;
  }
  .heart-div {
    top: 20%;
    right: 15%;
  }
  .heart-div-2 {
    top: 10%;
    left: 16%;
  }
}
@media only screen and (max-width: 600px) {
  .content {
    width: 100%;
    border: 50px solid #f48f03;
    border-inline: none;
  }
  .heart-div {
    right: 5%;
  }
  .cat-main {
    width: 100%;
  }
  .section-1 {
    width: 100%;
  }
  .bracket-1,
  .bracket-2 {
    display: none;
  }
}
@media only screen and (min-width: 1500px) {
  .heart-div-2 {
    left: 13%;
  }
  .heart-div {
    right: 3%;
  }
}
@media only screen and (min-width: 1100px) {
  .section-1 {
    width: 60vw;
  }
}

* {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.cart-container {
  display: grid;
  grid-template-columns: 1fr 4fr;
  margin-top: 40px;
  grid-gap: 40px;
  position: relative;
}
.user-profile-section {
  background-color: #ff9500;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  color: white;
  padding-top: 10px;
}
.orders-flex {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px 20px;
  padding-right: 10px;
  margin-top: 35px;
}
.order-flex {
  display: flex;
  background-color: white;
  border: 10px solid #ff9500;
  border-top: 35px solid #ffffff;
  border-bottom: 30px solid white;
  z-index: 0;
  flex-direction: column;
  position: relative;
  margin-top: 65px;
}
.overlay {
  position: absolute;
  width: 106%;
  left: -10px;
  height: 113%;
  top: -1%;
  z-index: 2;
  transition: all 0.3s ease-in-out;
}
.order-flex:hover > .overlay {
  background-color: rgba(255, 136, 0, 0.2);
}
.order-desc {
  padding: 20px;
  width: 750px;
}
.order-desc span {
  margin: auto;
}
.order-img-div {
  position: relative;
  display: flex;
  justify-content: center;
}
.order-desc a {
  color: #476899 !important;
  /* display: block; */
}
.order-desc a:hover {
  text-decoration: underline;
}
.remove-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.remove {
  z-index: 1;
  font-size: 33px;
  background: none;
  color: white;
  margin-left: auto;
  cursor: pointer;
  font-weight: 900;
  transition: all ease-in-out 1s;
}
.empty-bg {
  padding: 10px;
  position: absolute;
  border-radius: 50%;
  top: 11px;
  transition: all ease-in 0.3s;
  background-color: #575859;
}
.remove-container:hover > .empty-bg {
  background-color: #d74700;
  transform: scale(1.5);
}
.remove-container:hover > .remove {
  transform: scale(1.5);
}
.price {
  position: absolute;
  left: -2%;
  top: 55%;
  padding: 5px;
  width: 65px;
  text-align: end;
  font-weight: bold;
  background-color: #ff9500;
  color: white;
  z-index: 3;
}

.total {
  position: absolute;
  color: #000000;
  top: 0%;
  width: 10%;
  left: 19%;
  background: white;
  border: 5px solid #ff9500;
  padding: 8px;
  z-index: 10;
  display: block;
}
#refresh {
  display: none;
}
.order-container {
  position: relative;
}
.info-div {
  position: absolute;
  width: 100%;
  top: 5%;
  display: flex;
  background: #ff9500;
  justify-content: space-between;
  border-inline: 1px solid #36363675;
  padding: 20px;
  align-items: center;
  color: white;
  box-shadow: rgb(0 0 0 / 25%) 0px 54px 55px, rgb(0 0 0 / 12%) 0px -12px 30px,
    rgb(0 0 0 / 12%) 0px 4px 6px, rgb(0 0 0 / 17%) 0px 12px 13px,
    rgb(0 0 0 / 9%) 0px -3px 5px;
  z-index: 1;
}
.buttons-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 5%;
  border-inline: 50px solid transparent;
  background: #ff9500;
  color: white;
}
.addBtn {
  z-index: 4;
  color: #42ef23;
  /* border: 5px solid #fff; */
  cursor: pointer;
}
.removeBtn {
  z-index: 4;
  color: #fa000c;
  /* border: 5px solid #313131; */
  cursor: pointer;
}
@media only screen and (max-width: 1108px) {
  .orders-flex {
    grid-template-columns: 1fr 1fr;
  }
  .total {
    position: absolute;
    color: #000000;
    top: 0%;
    width: 105px;
    left: 200px;
    background: white;
    border: 5px solid #ff9500;
    padding: 8px;
    z-index: 10;
    display: block;
  }
}
@media only screen and (max-width: 680px) {
  .orders-flex {
    grid-template-columns: 1fr;
  }
  .cart-container {
    margin-top: 100px;
  }
}

